import { likeService } from '@/services/likeService'

const state = {
  likes: [],
  serviceError: ''
}

const getters = {
  getLikes (state) {
    return state.likes
  },
  getServiceError (state) {
    return state.serviceError
  },
  getNextId (state) {
    return state.likes[Number(state.likes.lenght - 1)].id
  }
}

const mutations = {
  clearError (state) {
    state.serviceError = ''
  },
  setLikes (state, likes) {
    state.likes = likes
  },
  setLikeServiceError (state, message) {
    state.serviceError = message
  }
}

const actions = {
  async getLikes ({ commit }) {
    try {
      const likes = await likeService.getLikes()
      commit('setLikes', likes)
    } catch (error) {
      commit('setLikeServiceError', error.message)
    }
  },
  async addLike ({ commit }, like) {
    try {
      await likeService.addLike(like)
      const likes = await likeService.getLikes()
      commit('setLikes', likes)
    } catch (error) {
      commit('setLikeServiceError', error.message)
    }
  },
  async deleteLike ({ commit }, id) {
    try {
      await likeService.deleteLike(id)
      const likes = await likeService.getLikes()
      commit('setLikes', likes)
    } catch (error) {
      commit('setLikeServiceError', error.message)
    }
  }
}

export default {
  namespaced: true,
  state,
  actions,
  mutations,
  getters
}
