<template>
  <div class="login">
    <b-form @submit.prevent="login" class="container w-75 mt-5">
      <h1>Connexion</h1>
      <label for="email" class=" mt-3">Adresse Courriel</label>
      <input
        type="email"
        class="form-control"
        placeholder="courriel@courriel.com"
        id="email"
        v-model="email"
      />
      <label for="pass" class=" mt-3">Mot de passe</label>
      <input
        type="password"
        class="form-control"
        placeholder="Password"
        id="pass"
        v-model="password"
      />
      <button type="submit" class="btn btn-primary mt-3">
        Se connecter
      </button>
      <p class="mt-2 text-primary" @click="ToRegister()">Créer un compte</p>
    </b-form>
  </div>
</template>

<script>
export default {
  name: 'Login',
  created () {
    this.$store.commit('authentication/clearError')
  },
  data () {
    return {
      email: '',
      password: ''
    }
  },
  methods: {
    async login () {
      await this.$store.dispatch('authentication/login', {
        email: this.email,
        password: this.password
      })
      if (!this.authServiceError) {
        this.$router.push({
          name: 'Home'
        })
      }
    },
    ToRegister () {
      if (!this.authServiceError) {
        this.$router.push({
          name: 'Register'
        })
      }
    }
  },
  computed: {
    authServiceError () {
      return this.$store.state.authentication.authServiceError
    }
  }
}
</script>
