<template>
  <div>
    <b-container>
      <b-form @submit.prevent="register">
        <h1>Creation de compte</h1>
        <label>Nom</label>
        <b-form-input required v-model="name" type="text" data-test="name" />
        <label>Courriel</label>
        <b-form-input required v-model="email" type="text" data-test="email" />
        <label>Mot de passe</label>
        <b-form-input
          required
          v-model="password"
          type="password"
          data-test="password"
        />
        <b-button
          variant="primary"
          type="submit"
          class="mt-4"
          data-test="register"
          id='buttonRegister'
          >Se registrer</b-button
        >
      </b-form>
      <div v-if="authServiceError" data-test="error" id='errorMessage'>
        {{ authServiceError }}
      </div>
    </b-container>
  </div>
</template>

<script>
export default {
  created () {
    this.$store.commit('authentication/clearError')
  },
  data () {
    return {
      email: '',
      password: '',
      name: ''
    }
  },
  methods: {
    async register () {
      await this.$store.dispatch('authentication/register', {
        email: this.email,
        password: this.password,
        name: this.name
      })
      if (!this.authServiceError) {
        this.$router.push({
          name: 'Home'
        })
      }
    }
  },
  computed: {
    authServiceError () {
      return this.$store.state.authentication.authServiceError
    }
  }
}
</script>
