import { API } from '@/shared/config'
import requestInterceptor from '@/shared/requestInterceptor'
import { parseAxiosErrorToAppError } from '@/shared/errorHelper'

async function getTrails () {
  try {
    const response = await requestInterceptor.get(`${API}/trails/`)
    return response.data
  } catch (error) {
    throw parseAxiosErrorToAppError(error)
  }
}

async function getParks () {
  try {
    const response = await requestInterceptor.get(`${API}/parks/`)
    return response.data
  } catch (error) {
    throw parseAxiosErrorToAppError(error)
  }
}

async function getSegments () {
  try {
    const response = await requestInterceptor.get(`${API}/segments/`)
    return response.data
  } catch (error) {
    throw parseAxiosErrorToAppError(error)
  }
}

export const mapService = {
  getTrails,
  getParks,
  getSegments
}
