import { API } from '@/shared/config'
import requestInterceptor from '@/shared/requestInterceptor'
import { parseAxiosErrorToAppError } from '@/shared/errorHelper'

async function addLike (like) {
  try {
    const response = await requestInterceptor.post(`${API}/likes/`, like)
    return response
  } catch (error) {
    throw parseAxiosErrorToAppError(error)
  }
}

async function deleteLike (id, userId) {
  try {
    const response = await requestInterceptor.delete(`${API}/likes/${id}`)
    return response
  } catch (error) {
    throw parseAxiosErrorToAppError(error)
  }
}

async function getLikes () {
  try {
    const response = await requestInterceptor.get(`${API}/likes/`)
    return response.data
  } catch (error) {
    throw parseAxiosErrorToAppError(error)
  }
}

export const likeService = {
  addLike,
  deleteLike,
  getLikes
}
