import { mapService } from '@/services/mapService'

const state = {
  mapServiceError: '',
  currentTrailId: 20,
  parks: [],
  trails: [],
  segments: []
}

const getters = {
  getCurrentTrailId (state) {
    return state.currentTrailId
  }
}

const mutations = {
  clearError (state) {
    state.mapServiceError = ''
  },
  initializeParks (state, parks) {
    state.parks = parks
    state.mapServiceError = ''
  },
  initializeTrails (state, trails) {
    state.trails = trails
    state.mapServiceError = ''
  },
  initializeSegments (state, segments) {
    state.segments = segments
    state.mapServiceError = ''
  },
  setMapServiceError (state, message) {
    state.mapServiceError = message
  },
  setCurrentTrailId (state, id) {
    state.currentTrailId = id
  }
}

const actions = {
  async getParks ({ commit }) {
    try {
      const parks = await mapService.getParks()
      commit('initializeParks', parks)
    } catch (error) {
      commit('setMapServiceError', error.message)
    }
  },
  async getTrails ({ commit }) {
    try {
      const trails = await mapService.getTrails()
      commit('initializeTrails', trails)
    } catch (error) {
      commit('setMapServiceError', error.message)
    }
  },
  async getSegments ({ commit }) {
    try {
      const segments = await mapService.getSegments()
      commit('initializeSegments', segments)
    } catch (error) {
      commit('setMapServiceError', error.message)
    }
  }
}

export default {
  namespaced: true,
  state,
  actions,
  mutations,
  getters
}
