<template>
  <!-- v-bind:class="nav-link {active:$route.name == 'Home'} -->
  <nav class="navbar navbar-expand-md navbar-dark bg-dark">
    <div class="navbar-nav mr-auto">
      <router-link
        class="nav-link d-inline-block"
        v-bind:class="{ active: $route.name == 'Home' }"
        v-bind:to="{ name: 'Home' }"
        >Accueil</router-link
      >
    </div>
    <div class="navbar-nav ml-auto">
      <b-link @click="logout" v-if="isLoggedIn" class="nav-link d-inline-block">
        Se déconnecter
      </b-link>
      <div v-else>
        <router-link
          class="nav-link d-inline-block"
          v-bind:class="{ active: $route.name == 'Login' }"
          v-bind:to="{ name: 'Login' }"
        >
          Connexion
        </router-link>

        <router-link
          class="nav-link d-inline-block"
          v-bind:class="{ active: $route.name == 'Register' }"
          v-bind:to="{ name: 'Register' }"
        >
          Register
        </router-link>
      </div>
    </div>
  </nav>
</template>

<script>
export default {
  computed: {
    isLoggedIn () {
      return this.$store.getters['authentication/isLoggedIn']
    }
  },
  methods: {
    // https://stackoverflow.com/questions/52229947/activate-method-on-router-link-click-in-vue
    logout () {
      this.$store.dispatch('authentication/logout')
      this.$router.push({
        name: 'Login'
      })
    }
  }
}
</script>
