<template>
  <div class="home mt-3">
    <div id="heart" @click="like">
      <i v-if="hasBeenLiked" class="fa-solid fa-heart fa-4x"></i>
      <i v-else class="fa-regular fa-heart fa-4x"></i>
    </div>
    <p>{{ getLikesForTrail }}</p>
    <Span class="text-danger">{{ Error }}</Span>
    <Span class="text-danger">{{ error2 }}</Span>
    <Map />
  </div>
</template>

<script>
import Map from '@/components/Map'

export default {
  name: 'Home',
  components: { Map },
  async created () {
    this.$store.commit('authentication/clearError')
    this.$store.commit('likes/clearError')
    await this.$store.dispatch('likes/getLikes')
  },
  data () {
    return {
      Error: ''
    }
  },
  methods: {
    async like () {
      if (this.isLoggedIn()) {
        if (!this.hasBeenLiked) {
          await this.$store.dispatch('likes/addLike', {
            userId: this.$store.getters['authentication/getTokenUserId'],
            trailId: this.$store.getters['map/getCurrentTrailId'],
            id: this.$store.getters['likes/getNextLikeId']
          })
        } else {
          await this.$store.dispatch(
            'likes/deleteLike',
            Number(this.getLikeId())
          )
        }
        await this.$store.dispatch('likes/getLikes')
        this.Error = ''
      } else {
        this.Error = 'Connectez-vous pour acceder à cette fonctionnalité'
      }
    },
    getLikeId () {
      var likes = this.$store.getters['likes/getLikes']
      likes = likes
        .filter(
          likes =>
            likes.trailId === this.$store.getters['map/getCurrentTrailId']
        )
        .filter(
          likes =>
            likes.userId ===
            this.$store.getters['authentication/getTokenUserId']
        )
      return likes[0].id
    },
    isLoggedIn () {
      return this.$store.getters['authentication/isLoggedIn']
    }
  },
  computed: {
    authServiceError () {
      return this.$store.state.authentication.authServiceError
    },
    hasBeenLiked () {
      if (this.isLoggedIn()) {
        var likes = this.$store.getters['likes/getLikes']
        return (
          likes
            .filter(
              likes =>
                likes.trailId === this.$store.getters['map/getCurrentTrailId']
            )
            .filter(
              likes =>
                likes.userId ===
                this.$store.getters['authentication/getTokenUserId']
            ).length > 0
        )
      }
      return false
    },
    getLikesForTrail () {
      var likes = this.$store.getters['likes/getLikes']
      return likes.filter(
        likes => likes.trailId === this.$store.getters['map/getCurrentTrailId']
      ).length
    },
    error2 () {
      return this.$store.getters['likes/getServiceError']
    }
  }
}
</script>
