<template>
  <div class="map">
    <p class="w-100 text-center">
      Parc {{ currentTrail.park }} - Chemin {{ currentTrail.name }}
    </p>
    <div class="container w-25 mr-5 d-inline-block align-top">
      <select
        name="parks"
        id="parks"
        class="w-100"
        @change="changeCurrentPark($event.target.value)"
      >
        <option v-for="park in parks" v-bind:key="park.id" :value="park.id">{{
          park.name
        }}</option>
      </select>
      <select
        name="trails"
        id="trails"
        class="w-100 mt-5"
        multiple
        @change="changeCurrentTrailById($event.target.value)"
        size="8"
      >
        <option
          v-for="trail in currentParkTrails"
          v-bind:key="trail.id"
          :value="trail.id"
          >{{ trail.name }}</option
        >
      </select>
    </div>
    <div class="container w-50 d-inline-block">
      <l-map
        style="height: 400px;"
        :zoom="zoom"
        :center="center"
        :options="mapOptions"
        @update:center="centerUpdate"
        @update:zoom="zoomUpdate"
      >
        <l-tile-layer :url="url" :attribution="attribution" />
        <l-polyline
          v-for="segment in currentTrailSegments"
          v-bind:key="segment.id"
          :lat-lngs="segment.coordinates"
          :color="manageSegmentColor(segment.level)"
        ></l-polyline>
      </l-map>
    </div>
  </div>
</template>

<script>
import { latLng } from 'leaflet'
import { LMap, LTileLayer, LPolyline } from 'vue2-leaflet'

export default {
  name: 'Example',
  components: {
    LMap,
    LTileLayer,
    LPolyline
  },
  data () {
    return {
      zoom: 13,
      center: [49, -62],
      url: 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',
      attribution:
        '&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors',
      currentZoom: 11.5,
      currentCenter: latLng(49, -62),
      showParagraph: false,
      mapOptions: {
        zoomSnap: 0.5
      },
      polyline: {
        latlngs: [],
        color: 'black'
      },
      parks: [],
      trails: [],
      segments: [],
      currentParkId: null,
      currentParkTrails: [],
      currentTrail: null,
      currentTrailSegments: []
    }
  },
  methods: {
    zoomUpdate (zoom) {
      this.currentZoom = zoom
    },
    centerUpdate (center) {
      this.currentCenter = center
    },
    filterTrailsByPark (parkId) {
      return this.trails.filter(trail => trail.parkId === parkId)
    },
    filterSegmentsByTrail (trail) {
      return this.segments.filter(segment =>
        trail.segments.includes(segment.id)
      )
    },
    changeCurrentPark (parkId) {
      this.currentParkId = parkId
      this.currentParkTrails = this.filterTrailsByPark(this.currentParkId)
      if (this.currentParkTrails.length > 0) {
        this.changeCurrentTrail(this.currentParkTrails[0])
      }
    },
    changeCurrentTrailById (id) {
      this.$store.commit('map/setCurrentTrailId', Number(id))
      var trail = this.trails.filter(trail => trail.id === Number(id))[0]
      this.changeCurrentTrail(trail)
    },
    changeCurrentTrail (trail) {
      this.currentTrail = trail
      this.currentTrailSegments = this.filterSegmentsByTrail(this.currentTrail)

      this.center = this.currentTrailSegments[0].coordinates[0]
    },
    manageSegmentColor (difficulty) {
      console.log(difficulty)
      if (difficulty === 'Facile') {
        return 'green'
      } else if (difficulty === 'Intermédiaire') {
        return 'blue'
      } else if (difficulty === 'Difficile') {
        return 'orange'
      } else if (difficulty === 'Très difficile') {
        return 'red'
      } else {
        return 'black'
      }
    },
    sortTrails (a, b) {
      if (a.name < b.name) return -1
      return 1
    }
  },
  async created () {
    await this.$store.dispatch('map/getParks')
    await this.$store.dispatch('map/getTrails')
    await this.$store.dispatch('map/getSegments')
    if (!this.mapServiceError) {
      this.parks = this.$store.state.map.parks
      this.trails = this.$store.state.map.trails.sort(this.sortTrails)
      this.segments = this.$store.state.map.segments
      this.changeCurrentPark(this.parks[0].id)
    }
  },
  computed: {
    mapServiceError () {
      return this.$store.state.map.authServiceError
    }
  }
}
</script>
